@keyframes animatedBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animated-background1 {
  background: linear-gradient(-45deg, #d8c5c6, #85a0f9, #4ad85f, #e21898);
  background-size: 400% 400%;
  animation: animatedBackground 3s ease infinite;
}

.card {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  height: 400px;
}

.card:hover {
  transform: scale(1.05);
}

.card-img-top {
  width: 100%;
  height: 50%;
  border: #000000 outset;
}

.card-body {
  height: 500px; /* Set fixed height */
  overflow-y: auto; /* Enable vertical scrolling for overflow content */
  position: relative; /* Position for absolute positioning of button */
  padding: 15px;
}

.card-title {
  margin-top: 0;
}

.card-text {
  margin-bottom: 10px;
}

.btn-dark1 {
  position: sticky;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #0b0d0e;
  color: aliceblue;
  border: none;
  z-index: 1; /* Ensure button is above scrollbar */
}

.btn-dark1:hover {
  background-color: #040303;
  color: #f1f1f1;
  font-weight: bold;
}

/* Scrollbar styles */
.card-body::-webkit-scrollbar {
  width: 10px; /* Set scrollbar width */
}

.card-body::-webkit-scrollbar-track {
  background-color: #f1f1f1; /* Track color */
}

.card-body::-webkit-scrollbar-thumb {
  background-color: #888; /* Thumb color */
  border-radius: 5px; /* Rounded corners for thumb */
}

.card-body::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* Thumb color on hover */
}
