@keyframes blink {
  0%, 100% {
    opacity: 0.2;
  }
  50% {
    opacity: 1;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinner-container {
  position: relative;
  width: 100px;
  height: 100px;
  animation: spin 2s linear infinite;
  margin: auto;
}

.spinner-grow {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: currentColor;
  animation: blink 1s infinite;
}

.spinner-grow:nth-child(1) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(0deg) translate(40px) rotate(0deg);
  animation-delay: 0.1s;
}

.spinner-grow:nth-child(2) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg) translate(40px) rotate(-45deg);
  animation-delay: 0.2s;
}

.spinner-grow:nth-child(3) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg) translate(40px) rotate(-90deg);
  animation-delay: 0.3s;
}

.spinner-grow:nth-child(4) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(135deg) translate(40px) rotate(-135deg);
  animation-delay: 0.4s;
}

.spinner-grow:nth-child(5) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(180deg) translate(40px) rotate(-180deg);
  animation-delay: 0.5s;
}

.spinner-grow:nth-child(6) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(225deg) translate(40px) rotate(-225deg);
  animation-delay: 0.6s;
}

.spinner-grow:nth-child(7) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(270deg) translate(40px) rotate(-270deg);
  animation-delay: 0.7s;
}

.spinner-grow:nth-child(8) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(315deg) translate(40px) rotate(-315deg);
  animation-delay: 0.8s;
}
