/* Define keyframes for the animated background */
@keyframes moveBackground {
    0% {
      background-position: 0% 0%;
    }
    100% {
      background-position: 100% 100%;
    }
  }
  
  /* Apply the animated background to the element */
  .animated-background {
    /* Add your creative background patterns or images */
    background: linear-gradient(
      135deg,
      rgba(245, 219, 117, 0.8),
      rgba(255, 228, 220, 0.8),
      rgba(148, 255, 129, 0.8)
    );
    background-size: 200% 200%; /* Adjust background size as needed */
    animation: moveBackground 8s ease-in-out infinite alternate; /* Adjust duration and animation effects */
    position: relative; /* Ensure the container is positioned for absolute positioning of flares */
  }
  